import { useState } from 'react';
import { useSearchParams } from 'react-router-dom';
import axios from 'axios';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Avatar from '@mui/material/Avatar';
import Typography from '@mui/material/Typography';
import Footer from '../Footer';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import styled from 'styled-components';
import Stack from '@mui/material/Stack';
import FormHelperText from '@mui/material/FormHelperText';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import { createTheme, ThemeProvider } from '@mui/material/styles';


const InfoBox = styled.div`
    color: #777;
`;

export default function ResetAccount() {
    const [passwordState, setPasswordState] = useState('');
    const [passwordError, setPasswordError] = useState('');
    const [params] = useSearchParams();
    const token = params.get('token');
    const userId = params.get('userId');

    const theme = createTheme({
        palette: {
          primary: {
            main: "#337ab7",
          },
        },
    });

    const onhandlePost = async(data,event) => {
        try {
            const res = await axios.patch(`${process.env.REACT_APP_URL}/user/resetPassword`,data);
            if(res.data.result) {
                alert(' .');
            }

        }catch(err) {
            console.error(err.response.data.message);
            if(err.response.status === 401) {
                alert(err.response.data.message);
            }else {
                alert('  ');
                event.target.reset();
            }
        }

    }

    const onResetPassword = (e) => {
        e.preventDefault();
        const data = new FormData(e.currentTarget);
        const password = data.get('password');
        const rePassword = data.get('rePassword');

        //   
        if(!token) {
            alert('  .  ');
            e.target.reset();
            return;
        }

        //   
        const passwordRegex = /^(?=.*[a-zA-Z])(?=.*[!@#$%*])(?=.*[0-9]).{8,12}$/;
        if (!passwordRegex.test(password)) setPasswordState('   .');
        else setPasswordState('');

        //   
        if (password !== rePassword) setPasswordError('  .');
        else setPasswordError('');

        const resetData = { token, userId, password, rePassword };

        if (
            passwordRegex.test(password) && 
            password === rePassword
        ) {
            onhandlePost(resetData,e);
            e.target.reset();
        }
    }

    return (
        <Container 
            maxWidth="md" 
            sx={{
                pt: 8,
                height: "100vh"
            }}
        >
            <Box 
                sx={{
                    mb: 8,
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center"
                }}
            >
                <Avatar sx={{ m: 1, bgcolor: "#337ab7" }}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                     
                </Typography>
            </Box>
            <Box component="form" onSubmit={onResetPassword} sx={{mb:8}}>
                <TableContainer>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell 
                                    align= "center"
                                    colSpan={2} 
                                    sx={{
                                        borderTop: "2px solid #444",
                                        borderBottom: "1px solid #888",
                                        fontWeight: "700"
                                    }}
                                >
                                     
                                </TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            <TableRow>
                                <TableCell sx={{background:"#f9f9f9"}}>
                                    <Typography></Typography>
                                </TableCell>
                                <TableCell>
                                    <TextField name="password" type="password" size="small" error={passwordState !== "" || false} required/>
                                    <FormHelperText error>{passwordState}</FormHelperText>
                                    <InfoBox>
                                        <span> 8~12  +   (!@#$%*)   .</span>
                                    </InfoBox>
                                </TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row" sx={{background:"#f9f9f9", borderBottom:"1px solid #888"}}>
                                    <Typography noWrap> </Typography>
                                </TableCell>
                                <TableCell sx={{ borderBottom:"1px solid #888" }}>
                                    <TextField name="rePassword" type="password" size="small" error={passwordError !== "" || false} required/>
                                    <FormHelperText error>{passwordError}</FormHelperText>
                                </TableCell>
                            </TableRow>
                        </TableBody>
                    </Table>
                </TableContainer>
                <Stack
                    direction="row"
                    justifyContent="flex-end"
                    alignItems="center"
                    spacing={2}
                    sx={{
                        mt:3,
                        mb:2
                    }}
                >
                    <ThemeProvider theme={theme}>
                        <Button type="submit" variant="contained" size="small"></Button>
                    </ThemeProvider>
                </Stack>
            </Box>
            <Footer />
        </Container>

    );
}