const ASYNC_REQ = "async/REQUEST"; //  
const ASYNC_REQ_SUCCESS = "async/REQUEST_SUCCESS"; //  
const ASYNC_REQ_ERROR = "async/REQUEST_ERROR"; //  
const ASYNC_REQ_INIT = "async/INIT"; //

