export const setAsync = () => ({ type: ASYNC_REQ });
export const setAsyncSuccess = () => ({ type: ASYNC_REQ_SUCCESS });
export const setAsyncError = (error) => ({ type: ASYNC_REQ_ERROR, error });
export const setAsyncInit = () => ({ type: ASYNC_REQ_INIT });

const initialState = {
  loading: false,
  success: false,
  error: null
};

export default function asyncReqReducer(state = initialState, action) {
  switch (action.type) {
    case ASYNC_REQ:
      return {
        ...state,
        loading: true,
        success: false,
        error: null
      };
    case ASYNC_REQ_SUCCESS:
      return {
        ...state,
        loading: false,
        success: true,
        error: null
      };
    case ASYNC_REQ_ERROR:
      return {
        ...state,
        loading: false,
        success: false,
        error: action.error
      };
    case ASYNC_REQ_INIT:
      return {
        ...state,
        loading: false,
        success: false,
        error: null
      };  
    default:
      return state;
  }
}