import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableRow from '@mui/material/TableRow';
import TextField from '@mui/material/TextField';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import FormHelperText from '@mui/material/FormHelperText';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import styled from 'styled-components';


const InfoBox = styled.div`
    color: #777;
`;

export default function ModifyMemberPwd({isLogined, userId}) {
    const navigate = useNavigate();
    const [passwordChk, setPasswordChk] = useState('');
    const [newPasswordError, setNewPasswordError] = useState('');
    const [rePassworddError, setRePasswordError] = useState('');

    const theme = createTheme({
        palette: {
          primary: {
            main: '#337ab7',
          },
        },
    });

    const onhandlePost = async(data,event) => {
        try {
            const res = await axios.patch(`${process.env.REACT_APP_URL}/user/modifyMemberPwd`, data);
            if(res.data.isModified){
                alert('  ');
                navigate('/memberInfo');
            }

        }catch(err) {
            console.error(err.response.data.message);
            if(err.response.status === 301) {
                navigate('/',{ replace: true });
            }else if(err.response.status === 401) {
                alert(err.response.data.message);
                setPasswordChk('  .');
            }else {
                alert('  ');
                event.target.reset();
            }
        }
    }

    const modifyMemberPassword = (e) => {
        e.preventDefault();
        const data = new FormData(e.currentTarget);
        const result = {
            userId,
            password: data.get('password'),
            newPassword: data.get('newPassword'),
            rePassword: data.get('rePassword')
        };
        const { password, newPassword, rePassword } = result;

        //    
        if(!password) {
            setPasswordChk('  .');
            return;
        }
        
        //    
        const passwordRegex = /^(?=.*[a-zA-Z])(?=.*[!@#$%*])(?=.*[0-9]).{8,12}$/;
        if (!passwordRegex.test(newPassword)) setNewPasswordError('   .');
        else setNewPasswordError('');

        //   
        if (newPassword !== rePassword) setRePasswordError('  .');
        else setRePasswordError('');

        if (
            passwordRegex.test(newPassword) && 
            newPassword === rePassword &&
            isLogined
        ) {
            onhandlePost(result,e);
        }
    }

    return(
        <Box sx={{width:"100%"}}>
            <Typography variant="h6" sx={{ mb:2 }}> </Typography>
            <Box component="form" onSubmit={modifyMemberPassword}>
                <TableContainer>
                    <Table>
                        <TableBody>
                            <TableRow>
                                <TableCell sx={{ background:"#f9f9f9", borderTop:"2px solid #444", width:"10%", textAlign:"center" }}>
                                    <Typography></Typography>
                                </TableCell>
                                <TableCell sx={{ borderTop:"2px solid #444" }}>{userId}</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell sx={{background:"#f9f9f9"}}>
                                    <Typography> </Typography>
                                </TableCell>
                                <TableCell>
                                    <TextField name="password" type="password" size="small" error={passwordChk !== ""} required/>
                                    <FormHelperText error>{passwordChk}</FormHelperText>
                                    <InfoBox>
                                        <span>      .</span>
                                    </InfoBox>
                                </TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell sx={{background:"#f9f9f9"}}>
                                    <Typography> </Typography>
                                </TableCell>
                                <TableCell>
                                    <TextField name="newPassword" type="password" size="small" error={newPasswordError !== ""} required/>
                                    <FormHelperText error>{newPasswordError}</FormHelperText>
                                    <InfoBox>
                                        <span> 8~12  +   (!@#$%*)   .</span>
                                    </InfoBox>
                                </TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell component="th" scope="row" sx={{background:"#f9f9f9"}}>
                                    <Typography noWrap> </Typography>
                                </TableCell>
                                <TableCell>
                                    <TextField name="rePassword" type="password" size="small" error={rePassworddError !== ""} required/>
                                    <FormHelperText error>{rePassworddError}</FormHelperText>
                                </TableCell>
                            </TableRow>
                        </TableBody>
                    </Table>
                </TableContainer>
                <Stack
                    direction="row"
                    justifyContent="flex-end"
                    alignItems="center"
                    spacing={2}
                    sx={{
                        mt:3,
                        mb:2
                    }}
                >
                    <ThemeProvider theme={theme}>
                        <Button type="submit" variant="contained" size="small"></Button>
                        <Button variant="contained" size="small" onClick={()=>navigate(-1)}></Button>
                    </ThemeProvider>
                </Stack>
            </Box>
        </Box>
    );
}